/* About us section styles*/

/* Mill animation */

@keyframes rotate-animation {
    0% {
        transform: rotate(0deg);
    }
    50% {
        transform: rotate(180deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.section-about-us {
    background-color: #FF97B3;

    & .row.section-about-us--row {
        display: flex;
        justify-content: space-between;
        @include mq-inv-tablet {
            flex-direction: column;
        }
    }

    & .column--half-width {
        width: calc(50% - 1rem);
        margin-right: 1rem;
        @include mq-inv-tablet {
            width: 100%;
            margin-bottom: 1rem;
        }
        &.column--with-animation {
            padding-top: 261px;
        }
        &:last-child {
            margin-right: 0;
        }
    }

    & .about-us--animation-block-wrapper {
        width: 100%;
        height: 100%;
        position: relative;
        & .about-us--img-mill-wings {
            position: absolute;
            background: url("../../img/mill-wings.svg");
            background-size: cover;
            top: -55%;
            left: 2%;
            animation: rotate-animation 10s infinite linear;
            transform-origin: 161px 261px;
        }
    }

    & .section-about-us--card {
        padding: 60px 50px;
        background-color: #780D1A;
        border-radius: 20px;
        color: #FF97B3;
        position: relative;
        height: 100%;
        & .about-us-decor-triangle {
            position: absolute;
            &.about-us-decor-triangle--top {
                border-left: 20px solid transparent;
                border-right: 20px solid transparent;
                border-top: 35px solid currentColor;
                top: 0;
                left: 50%;
                transform: translateX(-50%);
            }
            &.about-us-decor-triangle--right {
                border-right: 35px solid currentColor;
                border-top: 20px solid transparent;
                border-bottom: 20px solid transparent;
                top: 50%;
                right: 0;
                transform: translateY(-50%);
            }
            &.about-us-decor-triangle--bottom {
                border-left: 20px solid transparent;
                border-right: 20px solid transparent;
                border-bottom: 35px solid currentColor;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
            }
            &.about-us-decor-triangle--left {
                border-left: 35px solid currentColor;
                border-top: 20px solid transparent;
                border-bottom: 20px solid transparent;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
            }
        }
    }

    & .section-about-us--title {
        font-size: 80px;
        line-height: 92px;
        padding-bottom: 32px;
        margin-bottom: 32px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.8);
        color: #ffffff;
    }

    & .section-about-us--paragraph {
        margin-bottom: 30px;
        font-size: 21px;
        line-height: 32px;
        color: rgba(255, 255, 255, 0.8);
    }

    & .section-about-us--button {
        background-color: #FF97B3;
        padding: 16px;
        border-radius: 30px;
        max-width: 283px;
        width: 100%;
        color: #000000;
        font-style: italic;
        font-size: 20px;
        line-height: 23px;
        letter-spacing: 0.4px;
        &:hover {
            opacity: 0.8;
        }
    }
}